@import url('https://fonts.cdnfonts.com/css/dm-serif-text');

.journal-section {
    font-family: 'Times New Roman', Times, serif;
    background-color: #f9f9f9;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

.journal-container {
    background-color: white;
    padding: 20px 40px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 1000px;
    width: 100%;
    margin: 20px;
}

.journal-title {
    font-family: 'DM Serif Text', serif;
    font-size: 2.5em;
    margin-bottom: 20px;
    text-align: start;
}

.journal-premise {
    font-size: 1.2em;
    margin-bottom: 20px;
    text-align: start;
}

.journal-author,
.journal-date {
    font-size: 1em;
    text-align: start;
    color: #999;
}

.journal-date {
    margin-bottom: 5px;
}

.journal-content {
    display: grid;
    grid-template-columns: 20% 80%;
    flex-wrap: wrap;
    justify-content: space-between; 
    align-items: center; 
    margin: 20px 0;
}

.content-section {
    flex: 1; 
    display: flex;
    flex-direction: column;
    align-items: center; 
    justify-content: center;
    height: 100%;
}

.content-section img {
    width: 100%; /* Ensure the image takes up 100% width */
    max-width: 100%; /* Ensure the image doesn't exceed its original size */
    height: auto; /* Maintain aspect ratio */
    border-radius: 8px; /* Apply border radius if needed */
}

.content-section p {
    color: rgba(0, 0, 0, 0.7); /* Lighter shade between black and white */
    margin-top: 10px;
}

.content-section-description {
    flex: 2; /* Take up twice the space of .content-section */
}

.content-section-description p {
    color: black;
}

/* Specific styles for normal-content class */
.normal-content {
    display: block; /* Ensures normal block display */
    margin: 20px 0;
}

.journal-images {
    display: flex;
    flex-direction: column; /* Stack items vertically */
    align-items: center; /* Center items horizontally */
    margin: 20px;
}

.journal-image {
    margin-bottom: 20px;
    text-align: center;
}

.journal-image img {
    width: 100%; /* Ensure the image takes up 100% width */
    max-width: 100%; /* Ensure the image doesn't exceed its original size */
    height: auto; /* Maintain aspect ratio */
    border-radius: 8px; /* Apply border radius if needed */
}

.journal-image p {
    color: rgba(0, 0, 0, 0.7); /* Lighter shade between black and white */
    margin-top: 10px;
}

/* Additional styles for responsiveness */
@media (max-width: 768px) {
    .journal-content {
        display: grid; /* Stack items vertically on smaller screens */
        grid-template-columns: 1fr;

    }

    .content-section {
        align-items: center; /* Center items horizontally on smaller screens */
        text-align: center; /* Center text content */
    }

    .content-section-description {
        padding-left: 0; /* Remove padding on smaller screens */
        margin-top: 20px; /* Add margin between sections */
    }
}
